.Button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  margin: 5px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.4s, color 0.4s, transform 0.2s ease, box-shadow 0.2s ease;
  background: linear-gradient(45deg, #fbc12d, #f57f17);
  color: white;
  user-select: none; /* Evita que el contenido sea seleccionable */
  z-index: 1;
  text-shadow: 
  -1px -1px 0 #000,  
  1px -1px 0 #000,
  -1px 1px 0 #000,
  1px 1px 0 #000; /* Agrega borde negro a las letras */
}

.Button:hover {
  background: linear-gradient(45deg, #f57f17, #fbc02d);
}

.Button:active {
  transform: translateY(2px); /* Desplazamiento hacia abajo al hacer clic */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* Sombra más pronunciada al hacer clic */
}

.ButtonCallModal{
  background: linear-gradient(45deg, #92711c, #9e5210);
  padding: 20px 20px;
  position: absolute;
  right: 5%;
  bottom: 5%;
  border: 1px solid #00000075;
  border-radius: 50%;
}

.ButtonCallModal:hover{
  background: linear-gradient(45deg, #c09426, #ce6b14);
  animation: pulse 1s infinite; /* Aplica la animación pulsante */
}

@keyframes pulse {
  0% {
      transform: scale(1);
  }
  50% {
      transform: scale(1.1); /* Tamaño máximo en el centro del ciclo */
  }
  100% {
      transform: scale(1);
  }
}